html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--very-light-gray);
  font-family: "Public Sans", sans-serif;
  font-size: 16px;
  overflow-x: inherit;
}
body.noscroll {
  height: 100vh;
  overflow: hidden;
}

.container {
  max-width: 75rem;
  margin: 0 auto;
}
.container.container-pd-all {
  padding: 1.875rem 1.25rem;
}
.container.container-pd-y {
  padding: 5.5rem 1.25rem;
}
.container.container-pd-nav {
  padding: 0.938rem 1.25rem;
}

:root {
  --dark-blue: hsl(233, 26%, 24%);
  --lime-green: hsl(136, 65%, 51%);
  --bright-cyan: hsl(192, 70%, 51%);
  --grayish-blue: hsl(233, 8%, 62%);
  --light-grayish-blue: hsl(220, 16%, 96%);
  --very-light-gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
}

h1,
h2,
h3,
h4 {
  line-height: 1.1;
  font-weight: 400;
  color: var(--dark-blue);
}

h1 {
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
  font-size: 2.7rem;
}
@media (min-width: 64rem) {
  h1 {
    font-size: 3.5rem;
  }
}

h2 {
  margin-top: 0;
  margin-bottom: 1.3rem;
  font-size: 2.1rem;
}

h3 {
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
  font-size: 1.5rem;
}

h4 {
  margin-top: 0.8rem;
  margin-bottom: 0.8rem;
  font-size: 1.1rem;
}

p {
  margin: 0;
  line-height: 1.6;
  color: var(--grayish-blue);
}
@media (min-width: 64rem) {
  p {
    font-size: 1.15rem;
    line-height: 1.4;
  }
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li,
a {
  text-decoration: none;
}

li a {
  color: var(--grayish-blue);
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}
@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}
header {
  background-color: var(--white);
  position: relative;
  z-index: 1;
}
header .nav-bar {
  background-color: var(--white);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .nav-bar.open .overlay {
  display: block;
}
@media (min-width: 64em) {
  header .nav-bar.open .overlay {
    display: none;
  }
}
header .nav-bar.open .header-toggle > span:first-child {
  transform: rotate(45deg);
}
header .nav-bar.open .header-toggle > span:nth-child(2) {
  opacity: 0;
}
header .nav-bar.open .header-toggle > span:last-child {
  transform: rotate(-45deg);
}
header .nav-bar .overlay {
  display: none;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: linear-gradient(var(--dark-blue), transparent);
  z-index: -1;
}
header .nav-bar .overlay.invisible-element {
  visibility: hidden;
}
header .nav-bar .svg-logo img {
  display: block;
}
header .nav-bar .header-toggle span {
  display: block;
  width: 26px;
  height: 2px;
  background-color: var(--dark-blue);
  transition: all 0.4s ease;
  transform-origin: 2px 1px;
}
header .nav-bar .header-toggle span:not(:last-child) {
  margin-bottom: 6px;
}
header .nav-bar .nav-links {
  height: 100%;
  display: flex;
  align-items: center;
}
header .nav-bar .nav-links ul {
  margin: 0;
  padding: 0;
}
header .nav-bar .nav-links ul li {
  display: inline-block;
  padding: 0 10px;
}
header .nav-bar .nav-links ul li a {
  position: relative;
  transition: all 0.4s ease 0s;
}
header .nav-bar .nav-links ul li a:hover {
  color: var(--dark-blue);
}
header .nav-bar .nav-links ul li a:hover::before {
  transform: scaleX(1);
}
header .nav-bar .nav-links ul li a::before {
  content: "";
  position: absolute;
  bottom: -24px;
  left: 0px;
  transform: scaleX(0);
  width: 100%;
  height: 3px;
  background: linear-gradient(to right, var(--lime-green), var(--bright-cyan));
  transition: transform 0.4s ease 0s;
}
header .header-menu {
  background-color: var(--white);
  padding: 1rem;
  text-align: center;
  border-radius: 5px;
  position: absolute;
  margin-top: 25px;
  width: 90%;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
}
header .header-menu.invisible-element {
  visibility: hidden;
}
@media (min-width: 64em) {
  header .header-menu {
    display: none;
  }
}
header .header-menu ul {
  padding: 0;
}
header .header-menu ul li {
  margin: 20px 0;
}
header .header-menu ul li a {
  color: var(--dark-blue);
  font-weight: 400;
}

button {
  position: relative;
  height: 40px;
  width: 150px;
  border-radius: 50px;
  background-image: linear-gradient(to right, var(--lime-green), var(--bright-cyan));
  border: none;
  color: var(--white);
  transition: 0.4s ease;
  font-weight: 700;
  cursor: pointer;
  overflow: hidden;
}
button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.25);
  opacity: 0;
  transition: opacity 0.3s ease;
}
button:hover::before {
  opacity: 1;
}

@media (min-width: 64em) {
  .intro {
    display: flex;
    align-items: center;
  }
}
.intro .intro-img {
  position: relative;
  background-image: url(../../../images/bg-intro-mobile.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  min-height: 19rem;
}
@media (min-width: 40em) {
  .intro .intro-img {
    min-height: 25rem;
    background-position: center 63%;
  }
}
@media (min-width: 64em) {
  .intro .intro-img {
    flex: 3;
    order: 2;
    height: 40rem;
    background-image: none;
  }
}
@media (min-width: 64em) {
  .intro .intro-img::before {
    content: "";
    position: absolute;
    width: 170%;
    height: 100%;
    background-image: url("../../../images/bg-intro-desktop.svg");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 0% 75%;
    left: 4%;
  }
}
.intro .intro-img::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../../../images/image-mockups.png");
  background-size: 93%;
  background-repeat: no-repeat;
  background-position: center bottom;
}
@media (min-width: 40em) {
  .intro .intro-img::after {
    background-size: 60%;
  }
}
@media (min-width: 64em) {
  .intro .intro-img::after {
    width: 107%;
    height: 130%;
    background-size: 100%;
    background-position: center 150%;
    left: 34%;
  }
}
.intro .intro-text {
  text-align: center;
}
@media (min-width: 64em) {
  .intro .intro-text {
    flex: 2;
    order: 1;
    text-align: left;
  }
}
.intro .intro-text button {
  margin-top: 20px;
  margin-bottom: 40px;
}
@media (min-width: 64em) {
  .intro .intro-text button {
    margin-top: 30px;
  }
}

.features {
  background-color: var(--light-grayish-blue);
}
.features .upper {
  margin-bottom: 2.5rem;
  text-align: center;
}
@media (min-width: 64em) {
  .features .upper {
    margin-bottom: 15px;
    width: 56%;
    text-align: left;
  }
}
.features .upper h2 {
  padding: 0 50px;
}
@media (min-width: 64em) {
  .features .upper h2 {
    padding: 0;
  }
}
@media (min-width: 40em) {
  .features .upper p {
    padding: 0 3rem;
  }
}
@media (min-width: 64em) {
  .features .upper p {
    padding: 0;
  }
}
@media (min-width: 40em) {
  .features .lower {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.features .lower .feature-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
}
@media (min-width: 64em) {
  .features .lower .feature-card {
    align-items: flex-start;
    padding-right: 2.8rem;
  }
}
.features .lower .feature-card p {
  text-align: center;
}
@media (min-width: 64em) {
  .features .lower .feature-card p {
    font-size: 15px;
    text-align: left;
  }
}
@media (min-width: 40em) {
  .features .lower .feature-card {
    flex: 0 0 50%;
  }
}
@media (min-width: 64em) {
  .features .lower .feature-card {
    flex: 1;
  }
}

.latest-articles h2 {
  text-align: center;
}
@media (min-width: 64em) {
  .latest-articles h2 {
    text-align: left;
  }
}
.latest-articles .cards {
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .latest-articles .cards {
    margin-left: -25px;
  }
}
@media (min-width: 64em) {
  .latest-articles .cards {
    flex-direction: row;
    margin-left: -25px;
    margin-top: 30px;
  }
}
.latest-articles .cards .article-card {
  width: 100%;
  background-color: var(--white);
  margin-bottom: 1.25rem;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;
}
.latest-articles .cards .article-card:hover {
  transform: scale(1.05);
}
@media (min-width: 40em) {
  .latest-articles .cards .article-card {
    width: calc(50% - 25px);
    margin-left: 25px;
  }
}
@media (min-width: 64em) {
  .latest-articles .cards .article-card {
    width: calc(25% - 25px);
    margin-left: 25px;
  }
}
.latest-articles .cards .article-card .article-img {
  height: 12.5rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  flex-shrink: 0;
}
.latest-articles .cards .article-card .article-text {
  padding: 1.4rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}
@media (min-width: 64em) {
  .latest-articles .cards .article-card .article-text {
    padding: 1.2rem;
  }
}
.latest-articles .cards .article-card .article-text .author {
  font-size: 0.75rem;
}
.latest-articles .cards .article-card .article-text .title {
  cursor: pointer;
  transition: color 0.3s ease;
}
.latest-articles .cards .article-card .article-text .title:hover {
  color: var(--lime-green);
}
.latest-articles .cards .article-card .article-text .content {
  font-size: 0.875rem;
  line-height: 1.3;
  margin-bottom: 0.938rem;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  margin-top: auto;
}
@media (min-width: 64em) {
  .latest-articles .cards .article-card .article-text .content {
    margin-bottom: 0;
  }
}

.footer {
  background-color: var(--dark-blue);
}
.footer .footer-wrapper {
  text-align: center;
}
@media (min-width: 64em) {
  .footer .footer-wrapper {
    text-align: left;
    display: grid;
    grid-template-columns: 2fr repeat(2, 1fr) 4fr;
    grid-template-rows: 1fr auto;
    column-gap: 25px;
    grid-template-areas: "logo links1 links2 btn" "icons links1 links2 copyright";
  }
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-logo {
    grid-area: logo;
  }
}
.footer .footer-wrapper .footer-logo img {
  margin-bottom: 20px;
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-logo img {
    margin-top: 10px;
  }
}
.footer .footer-wrapper .social-icons {
  margin-bottom: 20px;
}
@media (min-width: 64em) {
  .footer .footer-wrapper .social-icons {
    grid-area: icons;
    justify-self: start;
    align-self: center;
  }
}
.footer .footer-wrapper .social-icons ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.footer .footer-wrapper .social-icons ul li {
  padding: 0 7px;
}
.footer .footer-wrapper .social-icons ul li a:hover svg path {
  fill: var(--lime-green);
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-links.column1 {
    grid-area: links1;
  }
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-links.column2 {
    grid-area: links2;
  }
}
.footer .footer-wrapper .footer-links a {
  line-height: 2.5;
}
.footer .footer-wrapper .footer-links a:hover {
  color: var(--lime-green);
}
.footer .footer-wrapper .footer-btn {
  margin: 20px 0;
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-btn {
    grid-area: btn;
    justify-self: end;
  }
}
.footer .footer-wrapper .footer-copyright {
  margin: 20px 0;
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-copyright {
    grid-area: copyright;
    justify-self: end;
  }
}
@media (min-width: 64em) {
  .footer .footer-wrapper .footer-copyright p {
    font-size: 0.8rem;
  }
}